/* Mobile */
@media screen and (max-width: 768px) {
    .sub-Interfaces {
        font-family: 'Heebo', sans-serif;
        font-size: 5vmin;
        color: grey;
        text-align: center;
        margin-top: 12vh;
        width: 90%;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        direction: rtl;
    }

    .Interfaces-contect {
        font-family: 'Heebo', sans-serif;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        direction: rtl;
        margin-top: 13vh;
        height: 34vh;
    }

    .title-Interfaces {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        width: 100vw;
        height: 6vh;
        color: white;
        font-weight: bold;
        text-align: center;
        padding-top: 2vh;
        font-size: 5vmin;
        margin-top: 5vh;
    }

    #title1-Interfaces {
        background-color: #175a85;
    }

    #title2-Interfaces {
        background-color: #1cb4e3;
    }

    .text-Interfaces {
        width: 93vw;
        height: 20vh;
        background-color: #e9e9e9;
        color: #000000;
        margin-bottom: 1vh;
        padding-right: 2vh;
        margin-top: 9vh;
        padding-left: 2vh;
        padding-top: 6vh;
    }

    .btn-div-Interfaces {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        display: flex;
        margin-top: 86vh;
    }

    .btn-Interfaces {
        font-family: 'Heebo', sans-serif;
        margin-right: 2vh;
        margin-bottom: 4vh;
        color: white;
        position: relative;
        width: 38vw;
        height: 3vh;
        padding: 1vh;
        text-align: center;
        font-size: 4vmin;
        font-weight: bold;
        border-radius: 15vw;
    }

    #next-btn-Interfaces {
        background-color: #1cb4e3;
    }

    #prev-btn-Interfaces {
        background-color: #b7b7b7;
    }

    .Interfaces-back {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        margin-top: 97vh;
        width: 100%;
    }

    #stpe1btn-Interfaces {
        color: #56c3e8;
        font-size: 5vw;
        text-align: center;
        text-decoration: underline;
        font-family: 'Heebo', sans-serif;
        margin-bottom: 5vh;
    }

    #btn-print-relations2 {
        position: absolute;
        background-color: transparent;
        width: 100vw;
        height: 7vh;
        background-color: rgb(151, 151, 151);
        margin-top: 110vh;
    }

    .print-img-relations2 {
        position: relative;
        top: 1.25vh;
        left: 21vw;
        width: 7vw;
    }

    .text-print-relations2 {
        position: relative;
        top: -5vh;
        left: -3vw;
        font-size: 4vmin;
        text-decoration: underline;
        color: #ffffff;
        font-weight: bold;
        font-family: 'Heebo', sans-serif;
    }
}

