.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #175a85;
    z-index: 2000;
    text-align: right;
    padding-top: 6vh;
    overflow: scroll;
}

.whiteLogo-menu {
    width: 47%;
    height: auto;
    position: absolute;
    top: 7vh;
    left: 25vw;
    padding: 2vw;
}

.menu-title {
    font-size: 8vw;
    color: white;
    margin-bottom: 5vh;
    margin-top: 19vh;
    margin-right: 4vw;
    text-align: center;
    padding: 5vw;
}

.closeBtn-menu {
    position: fixed;
    top: 2vh;
    left: 2vw;
    width: 15%;
    height: auto;
}

.menu-list {
    list-style-type: none;
    padding: 0;
    margin-top: 2vh;
}

.menu-list li {
    color: white;
    margin-bottom: 3vh;
    padding: 1.5vh 0;
    text-align: center;
    font-size: 5vw;
}

#last-item-m {
    margin-bottom: 8vh;
}

.lineMenu {
    border-bottom: 0.2vh solid white;
    width: 86vw;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    margin-top: -2vh;
}

#lineMenuLast {
    margin-top: -7vh;
}

.menu-list li.active {
    background-color: white;
    color: #175a85;
}

.mashov-menu {
    font-family: 'Heebo', sans-serif;
    font-size: 4vw;
    color: white;
    text-align: center;
    position: absolute;
    margin-top: 0vh;
    right: 50%;
    transform: translateX(50%);
    width: 67vw;
}

.mashovTextMenu {
    margin-bottom: 6vh;
}

#linkMenu {
    color: white;
}